<script>
import GxIcon from '@components/_Core/Icons/GxIcon'
import GxAvatar from '@components/_Core/Avatars/GxAvatar'

export default {
  name: 'GxCardAccountingCompany',
  data() {
    return {}
  },
  components: {
    GxIcon,
    GxAvatar,
  },
  props: {
    client: {
      type: Object,
      default: null,
    },
  },
  created() {
    console.log(this.client)
  },
  computed: {
    extractUsers() {
      return this.client.usersWithPermissions.filter((item, idx) => idx <= 3)
    },
    countAdditionalUsers() {
      return '+' + (this.client.usersWithPermissions.length - 4)
    },
  },
  methods: {
    changeFavorite() {
      this.client.favorite = !this.client.favorite
    },
    avatarInitials(completeName) {
      const nameParts = completeName.split(' ')
      if (nameParts.length > 1) {
        return nameParts[0][0] + nameParts[1][0]
      }
      return nameParts[0][0]
    },
    onClickCard() {
      if (
        this.client.accountingFirmClientStatus === 'activeWithoutConnection'
      ) {
        localStorage.setItem('clientNumber', this.client.clientNumber)
        this.$router.push({ name: 'homeAccountingView' })
      } else {
        console.log('goToConfig')
      }
    },
  },
}
</script>
<template>
  <li class="inline-flex pr-5 w-112">
    <div
      @click="onClickCard"
      :clientNumber="client.clientNumber"
      class="flex flex-grow mb-4 border-l-8 rounded cursor-pointer h-42 gxOutter de:mb-0 shadow-2 active:shadow-4 min-w-40 de:hover:bg-gray-200 todos-list-item border-secondary"
    >
      <div class="flex flex-col flex-grow p-4 overflow-hidden">
        <div class="flex pt-1 text-gray-500">
          <span class="flex text-sm">
            {{ client.taxId }}
          </span>
          <GxIcon
            class="flex justify-end flex-grow w-5 hover:font-bold"
            :class="{
              'text-avatar-21': client.favorite,
              '-mr-3': client.favorite,
            }"
            :icon="client.favorite ? 'start' : 'star_outline'"
            @click.prevent="changeFavorite"
          />
        </div>
        <div class="pt-1">
          <span class="text-sm font-bold">
            {{ client.legalName }}
          </span>
        </div>
        <div class="pt-1">
          <span class="text-xm">
            {{ client.commercialName }}
          </span>
        </div>
        <div class="flex flex-grow pt-10 pl-6 ">
          <div v-for="(user, index) in extractUsers" :key="index">
            <GxAvatar
              v-if="!user.image"
              :class="`bg-avatar-${user.color}`"
              class="text-white mr-4 -ml-5 rounded-full text-2xs"
              style="width: 1.5rem; height: 1.5rem;"
              >{{ avatarInitials(user.completeName).toUpperCase() }}</GxAvatar
            >
            <GxAvatar
              v-else
              class="mr-4 -ml-5 rounded-full"
              style="width:
            1.5rem; height: 1.5rem;"
              :avatar-img="user.image"
            />
          </div>

          <GxAvatar
            v-if="this.client.usersWithPermissions.length > 4"
            class="mr-4 -ml-5 text-white rounded-full circle"
            style="width: 1.5rem; height: 1.5rem;"
          >
            {{ countAdditionalUsers }}
          </GxAvatar>

          <span class="flex justify-end flex-grow">
            {{ client.profile }}
          </span>
        </div>
      </div>
    </div>
  </li>
</template>
<style lang="scss">
.circle {
  background: var(--primary);
}
</style>
